.contenedor-navbar {
    padding: 1rem 0;
}

.navbar {
    transition: all .2s ease;
    padding: 0 1rem !important;
}

@media (min-width: 768px) {
    .navbar {
        padding: .5rem 1rem !important;
    }
}

.navbar-dark .navbar-toggler-icon {
    padding: 1.8rem !important;
}

.scrolled {
    background-color: rgba(12, 34, 57, 0.9);
}

.logo, .logo img {
    box-sizing: content-box;
    width: 50px;
}

@media (min-width: 768px) {
    .logo, .logo img {
        box-sizing: content-box;
        width: 80px;
    }
}

#collapsibleNavbar {
    background: rgba(12, 34, 57, 0.9);
    padding: 2.5rem 0 1rem 0;
}

@media (min-width: 768px) {
    #collapsibleNavbar {
        background: transparent;
    }
}

#collapsibleNavbar ul {
    box-sizing: border-box;
    list-style-type: none;
}

#collapsibleNavbar ul li {
    padding: 1.2rem 1.5rem;
    cursor: pointer;
    font-family: Raleway;
    font-style: normal;
    font-weight: bold;
    font-size: 1.8rem;
    line-height: 1rem;
    color: #FFFFFF;
    margin-right: 2rem;
}

#collapsibleNavbar ul li:last-of-type {
    margin-right: 0;
}

#collapsibleNavbar ul li:hover {
    color: white;
}

#collapsibleNavbar ul li a {
    text-decoration: none;
    color: inherit;
}