:root {
  --plyr-color-main: #4dc6f3;
  --plyr-captions-background: #0c2239;
  --plyr-control-icon-size: 1.5rem;
  --plyr-control-spacing: 1rem;
  --plyr-control-radius: 50px;
  --plyr-video-controls-background: #0c2239;
  --espacio-superior-cuerpo-responsive: 76.25px;
  --espacio-superior-cuerpo: 116px;
  --bottom-navbar-height: 8rem;
}

@media (min-width: 768px) {
  :root {
    --plyr-control-icon-size: 2.5rem;
    --plyr-control-spacing: 2.5rem;
    --espacio-superior-cuerpo-responsive: 86px;
  }
}

body {
  font-family: Raleway, Roboto, Arial, Helvetica, sans-serif !important;
  font-size: 1.6rem;
  line-height: 1.5;
  color: rgba(12, 34, 57);
}

html {
  color: white;
  background-color: #4dc6f3;
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
  /** Reset para REMS - 62.5% = 10px de 16px **/
}

*,
*:before,
*:after {
  box-sizing: inherit;
  /*padding: 0;*/
  margin: 0;
}

/** Globales **/

p {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 1.5rem;
  color: #212529;
}

img {
  max-width: 100%;
}

.contenedor {
  max-width: 144rem !important;
  width: 90%;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .contenedor {
    width: 85%;
  }
  .contenedor-60 {
    width: 60%;
  }
}

.contenedor-85 {
  width: 85%;
}

.vh-50 {
  min-height: 100vh;
}

.clamp {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  min-height: unset !important;
  overflow-wrap: break-word;
  word-break: break-all;
}

.clamp-2 {
  -webkit-line-clamp: 2;
}

.clamp-3 {
  -webkit-line-clamp: 3;
}

.no-break {
  overflow-wrap: unset !important;
  word-break: keep-all !important;
}

.card {
  border: none !important;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 0px 2px 0 rgba(0, 0, 0, 0.19);
  transition: all 500ms ease-in-out;
}

.card:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 12px 20px 0 rgba(0, 0, 0, 0.19);
}

.card-img-top {
  width: 100%;
  height: 100%;
}

.card-title {
  font-size: 1.8rem;
  font-weight: 700;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  -webkit-line-clamp: 2;
}

@media (min-width: 768px) {
  .card-title {
    -webkit-line-clamp: 1;
  }
}

.card-text {
  font-size: 1.5rem;
  height: 4.4rem;
  overflow: hidden;
}

a,
a:hover,
a:focus {
  text-decoration: none !important;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/** Utilidades **/

.text-align-center {
  text-align: center !important;
}

.color-dark-blue {
  color: #212529 !important;
}

.full-background {
  position: fixed;
  opacity: 70%;
  background-position: 50%;
  background-size: cover;
  filter: brightness(80%) contrast(100%) saturate(100%);
  -webkit-filter: brightness(80%) contrast(100%) saturate(100%);
  height: 100%;
  width: 100%;
  z-index: 0;
}

.full-background::before {
  content: "";
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(5px);
  /* apply the blur */
  backdrop-filter: blur(5px);
  /* apply the blur */
  pointer-events: none;
  /* make the pseudo class click-through */
}

.stretch {
  width: 100% !important;
}

.justify-self-right {
  margin-left: auto !important;
}

.fw-300 {
  font-weight: 300;
}

.fw-600 {
  font-weight: 600;
}

.fw-700 {
  font-weight: 700;
}

.p-0 {
  padding: 0 !important;
}

.py-08 {
  padding-top: 0.8rem !important;
  padding-bottom: 0.8rem !important;
}

.m0-auto {
  margin: 0 auto !important;
}

.centrar-texto {
  text-align: center;
}

.d-block {
  display: block !important;
}

.table,
.modal {
  font-size: 1.4rem;
}

.table tr {
  transition: all 200ms ease-in-out;
}

.r-180 {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.position-relative {
  position: relative !important;
}

.boton {
  color: white;
  font-weight: 700;
  text-decoration: none;
  font-size: 1.5rem;
  padding: 0.7rem 3rem;
  margin-top: 1rem;
  display: inline-block;
  text-align: center;
  border-radius: 50px;
  transition: all 100ms ease-in-out;
}

.btn-principal {
  display: block;
  margin: 0;
  color: white;
  text-align: center;
  border: none;
  -webkit-user-select: none;
          user-select: none;
  border-radius: 20px;
  /*padding: .8rem 0;*/
  background-color: rgb(0 57 117);
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.2);
}

@media (min-width: 768px) {
  .btn-principal {
    display: inline-block;
    padding: 0.6rem 3rem;
  }
}

.btn-principal:hover {
  background-color: #4dc6f3;
  color: white;
}

.boton:hover {
  text-decoration: none;
}

.seccion {
  padding: 2rem 0;
  background: white;
  color: black;
}

@media (min-width: 768px) {
  .seccion {
    padding: 3rem 0 1.5rem 0;
  }
}

.cuerpo-pagina {
  padding-top: 76.25px;
  padding-top: var(--espacio-superior-cuerpo-responsive);
  min-height: 100vh;
  padding-bottom: 5rem;
}

@media (min-width: 768px) {
  .cuerpo-pagina {
    padding-top: 116px;
    padding-top: var(--espacio-superior-cuerpo);
  }
}

.titulo-seccion {
  font-size: 2.2rem;
  color: #0c2239e6;
  font-weight: 700;
  text-align: center;
  margin-top: 1.8rem;
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .titulo-seccion {
    font-size: 2.6rem;
    margin-top: 2.5rem;
    margin-bottom: 3.5rem;
  }
}

/*Inicio*/

.btn-alternador-vista {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 1.5rem;
  color: #0c2239;
  cursor: pointer;
  background: transparent;
  border: none;
}

.btn-alternador-vista:focus {
  outline: none;
}

.btn-alternador-vista:hover {
  text-decoration: none;
  color: inherit;
}

.btn-alternador-vista img {
  transition: all 500ms ease;
  width: 1rem;
  height: 1.4rem;
}

.rotacion-flechita {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

.courses-container {
  display: flex;
  justify-content: space-between;
}

.contact-form {
  margin: 10rem auto;
  margin-left: 40%;
}

.teacher-container {
  padding: 10%;
  justify-content: space-between;
  padding-bottom: 20%;
  height: 100%;
  width: 100%;
}

.header {
  height: 100%;
  width: 100%;
}

.carrusel {
  background-color: rgb(149, 151, 46);
  height: 100%;
  width: 100%;
}

/*Curso*/

.contenedor-portada-curso {
  height: 200px;
  position: relative;
}

.fondo-portada-curso {
  position: absolute;
  background-position: center center;
  background-size: cover;
  filter: brightness(110%) contrast(90%) saturate(50%);
  -webkit-filter: brightness(110%) contrast(90%) saturate(50%);
  height: 100%;
  width: 100%;
  z-index: 5;
}

.fondo-portada-curso::before {
  content: "";
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(8px);
  /* apply the blur */
  backdrop-filter: blur(8px);
  /* apply the blur */
  pointer-events: none;
  /* make the pseudo class click-through */
}

@media (min-width: 768px) {
  .contenedor-portada-curso {
    height: 300px;
  }
}

.contenedor-portada-curso img {
  position: absolute;
  display: block;
  height: 100%;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  z-index: 6;
}

.titulo-curso {
  font-size: 2.8rem;
}

.contenedor-controles-curso {
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .contenedor-controles-curso {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.descripcion-curso {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  color: #646d7b;
  margin-bottom: 1.8rem;
}

.descripcion-curso:last-of-type {
  margin-bottom: 2rem;
}

.subtitulo-descripcion-curso {
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
  font-weight: 600;
  color: #3a4048;
  margin-bottom: 2rem;
}

.requisitos-curso {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  color: #646d7b;
  margin-bottom: 2.5rem;
  padding-left: 3rem;
}

.btn-rep-curso {
  font-size: 1.5rem;
  padding: 0.7rem 4rem;
  border-radius: 30px;
  text-transform: uppercase;
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .contenedor-curso-profesor {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1.8rem;
  }
}

@media (min-width: 768px) {
  .contenedor-curso-profesor .contenedor-img {
    margin-right: 2rem;
  }
}

.contenedor-curso-profesor .contenedor-img img {
  border-radius: 50px;
  width: 70px;
  height: 70px;
  display: block;
  margin: 0 auto 1.4rem auto;
}

@media (min-width: 768px) {
  .contenedor-curso-profesor .contenedor-descripcion {
    flex: 1 1;
  }
}

.contenedor-curso-profesor h4 {
  font-family: "Montserrat", sans-serif;
  font-size: 1.8rem;
  color: #0c2239;
  text-align: center;
  font-weight: 600;
  margin-bottom: 1rem;
}

@media (min-width: 768px) {
  .contenedor-curso-profesor h4 {
    text-align: left;
  }
}

.contenedor-curso-profesor h4 a {
  text-decoration: none;
  color: #0c2239;
}

@media (min-width: 768px) {
  .btn-rep-curso {
    margin: 0;
  }
}

.contenedor-data-cursos {
  display: flex;
  justify-content: space-evenly;
}

@media (min-width: 768px) {
  .contenedor-data-cursos {
    flex-basis: 50%;
    justify-content: flex-end;
  }
}

.contenedor-data-cursos > div {
  font-size: 1.8rem;
  color: #3a4048;
  font-family: Roboto;
  font-weight: 500;
  text-transform: uppercase;
}

.contenedor-data-cursos > div i {
  font-size: 1.8rem;
  color: #071727;
}

/*Reproductor curso*/

#player {
  max-height: 95%;
}

.plyr--video .plyr__controls {
  padding: 0 !important;
}

.contenedor-reproductor {
  position: relative;
}

@media (min-width: 768px) {
  .contenedor-reproductor {
    display: flex;
    height: calc(100vh - 76.25px);
    height: calc(100vh - var(--espacio-superior-cuerpo-responsive));
  }
}

.contenedor-reproductor .contenedor-video {
  width: 100%;
  background: #000;
}

@media (min-width: 768px) {
  .contenedor-reproductor .contenedor-video {
    position: relative;
    flex-basis: 75%;
  }
}

.contenedor-reproductor .contenedor-acordeon {
  height: 300px;
  overflow: auto;
}

@media (min-width: 768px) {
  .contenedor-reproductor .contenedor-acordeon {
    padding: 0;
    flex-basis: 25%;
    height: 100%;
    overflow: auto;
  }
}

/*Docentes*/

