  margin: 10px;
  margin-top: 5rem;
  margin-bottom: 10rem;
}

.name-teacher {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 3.5rem;
  line-height: 44px;
  text-align: center;
}

@media (min-width: 768px) {
  .name-teacher {
